import React from "react";
import PageNotFound from "./PageNotFound";
import { projects } from "../content/projects";

export default function ProjectScreen({ name }) {
	//Check if screen exists
	if (!projects.hasOwnProperty(name)){
		return (
			<PageNotFound />
		)
	}


  return (
    <main>
      <div>
        <h1>Hallo {name}</h1>
      </div>
    </main>
  );
}
